<template>
  <div class="pageView">
    <page-header @btn-action="openApp" />
    <div class="mainView">
      <div class="topContent">
        <span class="invitText">{{inviteInfo.ownerName}}（{{inviteInfo.formatPhone}}）邀请您成为</span>
        <span class="invitRoom">【{{inviteInfo.roomAllName}}】</span>
        <span class="invitText">的一员</span>
      </div>
      <div class="bottomContent">
        <span class="invitText">请输入验证码：</span>
        <div class="verifyCode">
          <van-row
            style="width:120px;"
            type="flex"
            justify="space-between"
            @click="showKeyBoardAction"
          >
            <van-col
              class="phoneItem"
              :span="3"
              v-for="i in 6"
              :key="i"
            >{{phoneDatas.length >= i ? phoneDatas[i-1] : ''}}</van-col>
          </van-row>
          <div
            class="sendBtn"
            @click="sendMsgCode"
            :class="{countDown : verifyCodeStr != '发送验证码' &&  verifyCodeStr != '重新发送'}"
          >{{verifyCodeStr}}</div>
        </div>
        <div class="nextStepBtn" @click="registAndBind">完成注册并绑定</div>
      </div>
      <div class="remark">
        <div class="custom-checkbox" @click="isAgreeprotocol = !isAgreeprotocol">
          <img
            :src="isAgreeprotocol ? require('../../assets/images/checkbox_select.png') : require('../../assets/images/checkbox_unselect.png')"
          />
        </div>
        <span style="margin-left:5px;color:#BDBDBD">我已经阅读并同意</span>
        <span style="color:#FB5768;">
          《
          <span style="text-decoration: underline" @click="clickedProtocol(1)">海信服务协议</span>》、《
          <span style="text-decoration: underline" @click="clickedProtocol(2)">隐私保护协议</span>》
        </span>
      </div>
    </div>
    <!-- 数字键盘 -->
    <van-number-keyboard
      :show="showKeyboard"
      @input="onInput"
      @delete="onDelete"
      @blur="showKeyboard = false"
    />
  </div>
</template>

<script>
import PageHeader from "../../components/WechatShare/PageHeader";
import {
  getPhoneValitService,
  addRoomMemberByPhone,
  inviteBindRoom
} from "../../service/shareService";
export default {
  name: "invitationRegist",
  components: {
    PageHeader,
  },
  data() {
    return {
      showKeyboard: false,
      isAgreeprotocol: false,
      phoneDatas: [],
      verifyCodeStr: "发送验证码",
      phoneNum: "",
      inviteInfo: {}
    };
  },
  computed: {
    messageCode() {
      let phoneNum = "";
      for (let num of this.phoneDatas) {
        phoneNum += num;
      }
      return phoneNum;
    }
  },
  mounted() {
    this.inviteInfo = JSON.parse(this.$route.query.data);
    this.phoneNum = this.inviteInfo.phone;
    this.androidShareOpenApp("myfamily?type=1"); //android 拉起APP
  },
  methods: {
    sendMsgCode() {
      if (this.verifyCodeStr != "发送验证码" && this.verifyCodeStr != "重新发送") {
        return;
      }
      let reqData = {
        PHONE: this.phoneNum
      };
      getPhoneValitService(reqData)
        .then(res => {
          if (res.success == false) {
            //失败
            this.$toast(res.msg);
          } else {
            //成功
            this.$toast("验证码已发送");
            let totalTime = 60;
            this.verifyCodeStr = totalTime + "秒";
            let clock = window.setInterval(() => {
              totalTime--;
              if (totalTime > 0) {
                this.verifyCodeStr = totalTime + "秒";
              } else {
                window.clearInterval(clock);
                //修改颜色
                this.verifyCodeStr = "重新发送";
              }
            }, 1000);
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    registAndBind() {
      if (this.messageCode.length == 0) {
        this.$toast("请输入验证码");
        return;
      }
      if (this.messageCode.length != 6) {
        this.$toast("请输入正确的验证码");
        return;
      }
      if (!this.isAgreeprotocol) {
        this.$toast("请勾选海信服务协议");
        return;
      }
      let param = {
        message: this.messageCode,
        phone: this.inviteInfo.phone,
        roomAllName: this.inviteInfo.roomAllName,
        roomCode: this.inviteInfo.roomCode,
        userCode: this.inviteInfo.ownerCode
      };
      inviteBindRoom(param)
        .then(res => {
          if (res.resultCode == "0" || res.resultCode == "10023") {
            this.$router.push({
              name:"invitationResult",
              query:{resultCode:res.resultCode,roomName:this.inviteInfo.roomAllName}
            })
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(error => {
          this.$toast(error);
        });
    },
    openApp() {
      let urlString = "myfamily?type=1";
      this.shareOpenApp(urlString);
    },
    showKeyBoardAction() {
      if (this.showKeyboard) return;
      this.showKeyboard = true;
    },
    //点击协议
    clickedProtocol: function(index) {
      //1：信我家协议 2：隐私保护协议
      let type = index == 1 ? 1 : 3;
      this.$router.push({
        name: "protocolPage",
        query: { pageType: type }
      });
    },
    onInput(key) {
      console.log(key);
      if (this.phoneDatas.length < 6) {
        this.phoneDatas.push(key);
      }
    },
    onDelete() {
      if (this.phoneDatas.length > 0) {
        this.phoneDatas.pop();
      }
    }
  }
};
</script>
