import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import ShareGoods from '../views/shareGoods/shareGoods.vue'
import ShareArticle from '../views/shareGoods/shareArticle.vue'
import ShareActivity from '../views/shareGoods/shareActivity.vue'
import VerifyRouter from './verifyRoomRouter'
import FlashSaleGoods from '../views/flashSale/flashSaleGoods.vue'
import SharePaper from '../views/questionPaper/sharePaper'
import InvitationRoot from '../views/inviteRoomMember/invitationRoot'
import InvitationRegist from '../views/inviteRoomMember/invitationRegist'
import InvitationResult from '../views/inviteRoomMember/invitationResult'
import ProtocolPage from '../views/inviteRoomMember/protocolPage'
Vue.use(VueRouter);


const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    children: [],
    meta: {
      title: '品质生活'
    }
  },
  {
    path: '/about',
    name: 'about',
    component: About
  },
  // 分享链接打开的商品详情
  {
    path: '/shareGoods/:id/',
    name: 'shareGoods',
    component: ShareGoods,
    // meta:{
    //   title:'【信我家】商品详情'
    // },
  },
  // 品质生活分享
  {
    path: '/shareArticle/:id/',
    name: 'shareArticle',
    component: ShareArticle,
    meta: {
      title: '在【信我家】，做个品质生活家～'
    },
  },
  //商品合辑分享
  {
    path: '/shareActivity/:id/',
    name: 'shareActivity',
    component: ShareActivity,
    meta: {
      title: '【信我家】海量优质商品来啦～'
    }
  },
  {
    path: '/shareSeckill/:id/',
    name: 'shareSeckill',
    component: FlashSaleGoods,
    meta: {
      title: '【信我家】限时秒杀你不来看看吗～'
    },
  },
  {
    path: '/sharePaper/:id',
    name: 'sharePaper',
    component: SharePaper,
    meta: {
      title: '【信我家】问卷调查'
    },
  },
  {
    path: '/shareRoom/:id',
    name: 'shareRoom',
    component: InvitationRoot,
    meta: {
      title: '【信我家】邀请加入'
    }
  },
  {
    path: '/invitationRegist',
    name: 'invitationRegist',
    component: InvitationRegist,
    meta: {
      title: '【信我家】邀请加入'
    }
  },
  {
    path: '/invitationResult',
    name: 'invitationResult',
    component: InvitationResult,
    meta: {
      title: '【信我家】邀请加入'
    }
  },
  {
    path: '/protocolPage',
    name: 'protocolPage',
    component: ProtocolPage,
    meta: {
      title: '【信我家】邀请加入'
    }
  }
]

routes.push(...VerifyRouter.routes)

const router = new VueRouter({
  routes
});

router.beforeEach((to, from, next) => {
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  next();
});

export default router


